@font-face {
  font-family: "Geometria";
  src: local("Geometria"), local("Geometria Regular"), local("Geometria-Regular"), url("../fonts/Geometria-Regular.woff2") format("woff2"), url("../fonts/Geometria-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Geometria", Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #8e80a9;
  background-color: #fcf8fe;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
a {
  text-decoration: none;
}
.page-header {
  position: relative;
}
.page-header::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 336px;
  background-image: url("../img/header-background-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 100%;
  z-index: -1;
}
@media (min-resolution: 2dppx) {
  .page-header::after {
    background-image: url("../img/header-background-mobile@2x.png");
  }
}
@media (min-width: 768px) {
  .page-header::after {
    height: 484px;
    background-image: url("../img/header-background-tablet@1x.png");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .page-header::after {
    background-image: url("../img/header-background-tablet@2x.png");
  }
}
@media (min-width: 1440px) {
  .page-header::after {
    background-image: url("../img/header-background-desktop@1x.png");
    height: 908px;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .page-header::after {
    background-image: url("../img/header-background-desktop@2x.png");
